import handyScroll from 'handy-scroll';

Drupal.behaviors.tableScroll = {
  attach(context) {
    const tables = context.querySelectorAll('.table__wrapper');
    // console.log(tables);
    Array.prototype.forEach.call(tables, (table) => {
      handyScroll.mount(table);
      const scrollbar = table.querySelector('.handy-scroll');
      scrollbar.setAttribute('tabindex', '0');
    });
  },
};
